.contact-bg{
    width: 100%;
    height: 54vh;
    background: #297f54;
    background-attachment: fixed;
    text-align: center;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact-bg h2{
    color: #fff;
    margin-top: 80px;
    font-size: 3.5rem;
    padding: 3px 3px;
    font-weight: 600;
}
.line div{
    margin: 0 0.2rem;
}

.text{
    font-size: 20px;
    font-weight: 400;
    opacity: 0.9;
}
.contact-bg .text{
    margin: 1.6rem 0;
}
.contact-body{
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 1rem;
}

.contact-info{
    background: #fff; 
    
    margin: 1rem 0;
    text-align: center;
    padding: 2rem 0;
}
.contact-info span{
    display: block;
}
.contact-info div{
    margin: 0.8rem 0;
    padding: 1rem;
}
.contact-info span .fas{
    font-size: 2rem;
    padding-bottom: 0.9rem;
    color: #297f54;
}
.contact-info div span:nth-child(2){
    font-weight: 100;
    font-size: 1.1rem;
}
.contact-info .text{
    padding-top: 0.4rem;
    color: black;
    font-weight: 600px;
    
}
.contact-form{
   
    padding: 2rem 1rem;
    border-top: 2px solid #c7c7c7;
   
}
.contact-form form{
    padding-bottom: 1rem;
}
.form-control{
    width: 100%;
    border: 1.5px solid #c7c7c7;
    border-radius: 5px;
    padding: 0.7rem;
    margin: 0.6rem 0;
    font-family: 'Times New Roman', Times, serif;
    font-size: 20px;
    outline: 0;
}
.form-control:focus{
    box-shadow: 0 0 6px -3px rgba(48, 48, 48, 1);
}
.contact-form form div{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 0.6rem;
}

.contact-form form h2{
    margin-bottom: 15px;
    font-size: 25px;
    color: #297f54;
}

.send-btn{
    font-family: 'Times New Roman', Times, serif;
    font-size: 1rem;
    text-transform: uppercase;
    color: #fff;
    background: #297f54;
    border: none;
    border-radius: 5px;
    padding: 0.7rem 1.5rem;
    cursor: pointer;
    transition: all 0.4s ease;
}
.send-btn:hover{
    opacity: 0.8;
    color: #fff;
    background-color: black;
}
.contact-form > div img{
    width: 70%;
}
.contact-form > div{
    margin: 0 auto;
    text-align: center;
}

.map iframe{
    align-items: center;
    margin-left: 260px;
}

@media screen and (min-width: 768px){
    .contact-bg .text{
        width: 70%;
        margin-left: auto;
        margin-right: auto;
    }
    .contact-info{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (min-width: 992px){
    .contact-bg .text{
        width: 50%;
    }
    .contact-form{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
    }
}

@media screen and (min-width: 1200px){
    .contact-info{
        grid-template-columns: repeat(4, 1fr);
    }
}
